import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule } from '@angular/forms';
import { UpdateStockComponent } from './update-stock.component';
const routes: Routes = [
  {
    path: '',
    component: UpdateStockComponent
  },
  {
    path: 'by-product-app',
    loadChildren: () => import('./by-product-app/by-product-app.module').then(m => m.ByProductAppModule)
  },
  {
    path: 'by-product-tiktok',
    loadChildren: () => import('./by-product-tiktok/by-product-tiktok.module').then(m => m.ByProductTiktokModule)
  },
]

@NgModule({
  declarations: [UpdateStockComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class UpdateStockModule { }
