import { Component } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-update-stock',
  templateUrl: './update-stock.component.html',
  styleUrls: ['./update-stock.component.scss']
})
export class UpdateStockComponent {

  constructor(
    private router: Router
  ) {

  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok'])
  }
  gotobyProductApp() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/update-stock/by-product-app'])
  }
  gotobyProductTiktok() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/tiktok/update-stock/by-product-tiktok'])
  }
}
